import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { MenuController, IonSlides } from '@ionic/angular';
import { ApiMongoService } from '../services/apiMongo.service';
import { StorageService } from '../services/storage.service';
import { environment as env } from 'src/environments/environment';
import { Funciones } from '../compartido/funciones';
import { AuthService } from '../services/auth.service';

@Component({
  selector: 'app-home',
  templateUrl: 'home.page.html',
  styleUrls: ['home.page.scss'],
})
export class HomePage implements OnInit {

  public submitAttempt: boolean = false;
  public loginForm: FormGroup;
  public load = true;
  public viewPass = false;
  public tipo = "password";

  constructor(
    private authService: AuthService,
    private _apiMongo: ApiMongoService,
    public _storage: StorageService,
    public router: Router,
    public formBuilder: FormBuilder,
    private _fun: Funciones,
    public menu: MenuController,) {

    this.loginForm = formBuilder.group({
      username: ['', Validators.compose([Validators.pattern("[^@]+@[^@]+\.[^@]+$"), Validators.required])],
      pass: ['', Validators.required]
    });
  }

  ngOnInit() {
    /*     this.menu.enable(false); */

    let data = {};
    console.log('typeof data',typeof data);
    
  }

  async login() {

    this.submitAttempt = true;
    if (this.loginForm.valid) {
      this.authService.signIn(this.loginForm.value);
    }

  }

  mostrar() {
    if (this.viewPass) {
      this.tipo = "password";
      this.viewPass = false;
    } else {
      this.tipo = "text";
      this.viewPass = true;
    }
  }
}
